@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Cairo', sans-serif, monospace;
  
  /* https://cssgradient.io/ */
  background: rgb(9,86,121);
  background: linear-gradient(90deg, rgba(9,86,121,1) 3%, rgba(137,255,204,1) 100%);
}
 button {
   cursor: pointer;
 }
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
.Tilt {
      /* https://cssgradient.io/ */
  background: rgb(9,86,121);
  background: linear-gradient(90deg, rgba(9,86,121,1) 3%, rgba(137,255,204,1) 100%);
}
.form {
    width: 700px;
    /* https://projects.verou.me/css3patterns/ - Shippo */
    background-color:#89ffcc;
    background-image: radial-gradient(closest-side, transparent 98%, rgba(0,0,0,.3) 99%),
    radial-gradient(closest-side, transparent 98%, rgba(0,0,0,.3) 99%);
    background-size:80px 80px;
    background-position:0 0, 40px 40px;
}
.App {
  text-align: center;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  left: 0;
  margin: 0;
  z-index: -1;
}
